document.addEventListener("DOMContentLoaded", function(event) {
    // The document is ready, you can execute your code here
    console.log("Document is ready!");

    // document.addEventListener('click', "a[href='#']", (event)=> {
    //     event.preventDefault();
    // });


    // Button to open menu on mobile devices
    const showHeader = document.querySelector('.actMobMeunu');
    const menuList = document.querySelector('.menuList');

    showHeader.addEventListener('click', ()=> {
       showHeader.children[0].classList.toggle('show')
       showHeader.children[1].classList.toggle('show')
       menuList.classList.toggle('active')
    });

    // Listener to pin the menu on scroll page
    const menu = document.querySelector('.header');
    // if(window.innerWidth > 1200) {}
    window.addEventListener('scroll', () => {
        const menuHeight = menu.offsetTop;

        if (window.scrollY > menuHeight) menu.classList.add('fixed');
        else menu.classList.remove('fixed');
    });

    // Control visibility of add to cart button
    const productCards = document.querySelectorAll('.productCard');
    productCards.forEach(productCard => {

        const showbuttons = productCard.querySelector('.btnsControl');
    
        productCard.addEventListener('mouseover', ()=> {
            showbuttons.classList.add('active')
        });

        productCard.addEventListener('mouseout', ()=> {
            showbuttons.classList.remove('active')
        });
    });
    

    // Function to control the float add to cart button in mobile devices
    const productArea = document.querySelector('.productInfos');
    const btnAddToCart = document.querySelector('.btnProduct');

    if(productArea && window.innerWidth < 1200 ) {
        const bottomProductArea = productArea.offsetTop + productArea.offsetHeight;
        window.addEventListener('scroll', () => {
            if (window.scrollY > bottomProductArea) btnAddToCart.classList.remove('fixed');
            else  btnAddToCart.classList.add('fixed')
        });
    }
});

